@font-face {
    font-family: "Belisa Plumilla Manual";
    src: url("../Fonts/belisa_plumilla.ttf") format('truetype');
}

@font-face {
    font-family: "Stint Ultra Expanded";
    src: url("../Fonts/StintUltraExpanded-Regular.ttf") format('truetype');
}

:root {
    --bg-of-div: rgba(255, 255, 255, 0.3);
    --bg-of-description: rgba(255, 255, 255, 0.25);
    --bg-of-creditsButton: rgba(255, 255, 255, 0.5);
    --bg-of-creditsButton_hover: rgba(255, 255, 255, 0.3);
    --backdrop-blur: blur(5px);
    --width-of-main: 82vw;
    --box-shadow-div: 0 0 12px black;
}

body {
    margin: 0;
}

.imageBackground {
    background-image: url("../Images/background-image.jpg");
    background-size: cover;
    background-position-y: center;
    min-height: 100vh;
    filter: brightness(0.85);
}

.containerMain {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main {
    position: absolute;
    background: linear-gradient(166deg, var(--bg-of-div), transparent);
    backdrop-filter: var(--backdrop-blur);
    width: var(--width-of-main);
    height: 90vh;
    top: 5vh;
    border-radius: 20px;
    box-shadow: var(--box-shadow-div);
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Belisa Plumilla Manual";
    font-size: 20px;
    position: relative;
    width: 73vw;
    top: 10vh;
    left: 4vw;
}

.title h2 {
    font-size: 40px;
    color: rgb(21, 21, 21);
    margin-bottom: 0px;
}

.title h1 {
    font-size: 85px;
}

.description {
    position: absolute;
    font-family: "Stint Ultra Expanded";
    font-size: 25px;
    left: 50%;
    transform: translateX(-50%) translateY(12vh);
    padding: 15px;
    background-color: var(--bg-of-description);
    width: 60vw;
    height: 30vh;
    border-radius: 20px;
    text-align: center;
    box-shadow: var(--box-shadow-div);
}

.allPages {
    position: absolute;
    display: flex;
    align-items: center;
    width: var(--width-of-main);
    height: 40px;
    top: 1vh;
    z-index: 1;
    border-bottom: 2px solid black
}

.allPages ul {
    list-style: none;
    display: flex;
    flex-direction: row;
}

.allPages ul li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3px;
    background-color: var(--bg-of-creditsButton);
    border-radius: 12px;
    border: 3px solid black;
    width: var(--width-of-button, 5.3vw);
    height: 25px;
    font-size: 20px;
}

.allPages ul li:hover {
    background-color: var(--bg-of-creditsButton_hover);
    border: 2px solid black;
    cursor: pointer;
}

.allPages ul li a {
    color: black;
    font-family: "Stint Ultra Expanded";
    font-size: 19px;
    text-decoration: none;
    margin: 5px;
}

.buttonOfCalcOfDays {
    --width-of-button: 12vw;
}

.startNowButton {
    margin-top: 45px;
    padding: 12px;
    background-color: var(--bg-of-creditsButton);
    border-radius: 12px;
    border: 2px solid black;
    font-size: 25px;
    box-shadow: 3px 4px 9px #2a2722;
}

.startNowButton:hover {
    cursor: pointer;
}


.section {
    position: absolute;
    font-family: "Stint Ultra Expanded";
    font-size: 25px;
    left: 50%;
    transform: translateX(-50%) translateY(12vh);
    padding: 15px;
    background-color: var(--bg-of-description);
    width: 60vw;
    height: 64vh;
    border-radius: 20px;
    text-align: center;
    box-shadow: var(--box-shadow-div);
}

.titleSection {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Belisa Plumilla Manual";
    font-size: 20px;
    position: relative;
    width: 73vw;
    height: 8vh;
    top: 10vh;
    left: 4vw;
}

.titleSection h1 {
    font-size: 55px;
}

#daysPassed_SearchBar {
    --box-shadow-div: 0 0 6px black;
    border: none;
    outline: none;
    width: 12vw;
    border-radius: 20px;
    padding: 10px;
    box-shadow: var(--box-shadow-div);
}

#daysPassed_SearchBar:hover, #daysPassed_SearchBar:focus {
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

.daysPassed_Button {
    --box-shadow-div: 0 0 6px black;
    border: none;
    position: relative;
    width: 4vw;
    height: 35px;
    border-radius: 10px;
    padding: 5px;
    box-shadow: var(--box-shadow-div);
}

.daysPassed_Button i {
    position: absolute;
    font-size: 20px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.daysPassed_Button:hover {
    background-color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

.dataText {
    position: relative;
    top: 10vh;
}

.daysPassed_Title {
    margin-bottom: 10px;
}

.daysPassed_Description {
    margin-top: 10px;
}